import React,{useState,useEffect} from 'react';
import Firebase from 'firebase';
import {auth,storage} from './firebaseInit';
import Loader from '../Reusable/loader/loader';
import Error from '../Reusable/error/error';
import Toast from '../Reusable/toast/toast';

const FirebaseBlogPublish = (props)=>{
  const [openDialog,setOpenDialog] = useState(false);
  const [error,setError] = useState();
  const [toast,setToast] = useState(false);
  const [message,setMessage] = useState()
  const [loading,setLoading] = useState(true);
  useEffect(()=>{
  	setLoading(true);
  	Firebase.auth().onAuthStateChanged(async(user)=>{
			if (user) {
				const db = await Firebase.firestore();
				db.collection('written-blogs').doc(user.uid).get()
				.then((doc)=>{
					if (doc.exists) {
						const uid = doc.data().uid;
						const blogs = doc.data().blogDetails;
						const publish = blogs.filter(element=>element.blogId === props.blogId);
						db.collection('published').doc(publish[0].blogId).set(publish[0]);
						setMessage("Published")
						setToast(true)
						setLoading(false)
						window.setTimeout(()=>{props.setBlogPublishFlag(false)},3000)
					}
					else{
						setError("No blogs found")
						setOpenDialog(true)
						setLoading(false)
						window.setTimeout(()=>{props.setBlogPublishFlag(false)},3000)
					}
				}) 
			}
			else{
				setError("No user Logged in.")
				setOpenDialog(true)
				setLoading(false)
				window.setTimeout(()=>{props.setBlogPublishFlag(false)},3000)
			}
		})
  },[])
    
    if (!loading) {
    	return(<div>
  		{
  			openDialog ? <Error error={error} openDialog={openDialog} /> : null
  		}
  		{
  			toast ? <Toast message={message} toast={toast} setToast={setToast} /> : null
  		}</div>)
    }
    else{
    	return(<Loader/>)
    }
  }
export default FirebaseBlogPublish;