import Alert from '@material-ui/lab/Alert';
import React ,{useState,useEffect} from 'react';
import {Snackbar} from '@material-ui/core';

const Toast=(props)=>{

  return(<Snackbar open={props.toast} autoHideDuration={3000} onClose={()=>props.setToast(false)} anchorOrigin={{vertical:'top',horizontal:'center'}}>
          <Alert onClose={()=>props.setToast(false)} severity="success">
            {props.message}
          </Alert>
        </Snackbar>)
}

export default Toast;