import React,{useState,useEffect} from 'react';
import {Box,Card,CardContent,CardHeader,CardActions,Button,
List,ListItem,makeStyles,Typography,Chip} from '@material-ui/core';
import Firebase from 'firebase';
import {auth,firestore} from '../../firebase/firebaseInit';
import Loader from '../../Reusable/loader/loader';
import {useHistory} from 'react-router-dom';
import FirebaseBlogPublish from '../../firebase/firebaseBlogPublish';

const Navbar = React.lazy(()=>import('../Navbar/navbar'));

const style=makeStyles((theme)=>({
	button:{
        fontWeight:'bold',
        padding:'1%',
        marginLeft:'2%',
        marginRight:'2%',
        color:'white',
        backgroundImage:'linear-gradient(to right, #00e600, #00e600)'
    },
    blogsection:{
    	width:'70%',
    	float:'left',
    	marginLeft:"2%",
    	marginTop:"2%",
    	['@media (max-width:600px)']:{
    		width:'100%',
    	}
    },
    blogs:{
    	float:'left',
    	width:'fit-content',
    },
    addBlog:{
    	float:'right',
    	margin:'2%',
    },
    chip:{
    	fontWeight:'bold',
    	padding:'1%',
    	color:'white',
    	fontSize:'15px',
        backgroundImage:'linear-gradient(to right, #00e600, #00e600)'
    },
    card:{
    	width:'fit-content',
    	boxShadow:'5px 5px 10px lightgrey',
    	'&:hover':{
    		boxShadow:'15px 15px 25px lightgrey',
    	}
    }
}))

const UserWrittenBlogs=(props)=>{
	const classes = style();
	const history = useHistory();
	const [blogArray,setBlogArray] = useState();
    const [blogPublishFlag,setBlogPublishFlag] = useState(false);
    const [blogId,setBlogId] = useState();
    useEffect(()=>{
        Firebase.auth().onAuthStateChanged(async(user)=>{
            if (user) {
                const db = await Firebase.firestore();
                db.collection("written-blogs").doc(user.uid).get()
                .then((doc)=>{
                    if(doc.exists){
                        setBlogArray(doc.data())
                    }
                })
                .catch((error)=>alert(error.message));
            }
            else{
                alert('No user Logged in.')
            }
        })
    },[])
    const handleBlog=()=>{
    	history.push('/MyProfileCard/writeblog');
    }
    const handleBlogRead=(props)=>{
    	history.push('/MyProfileCard/blog/'+props);
    }

    const handleBlogPublish=(props)=>{
        setBlogId(props);
        setBlogPublishFlag(true);
    }
	if (blogArray) {
		return(<div>
		<Navbar/>
		<Box className={classes.addBlog}>
		<Chip label="Want to write more blogs." onClick={handleBlog} className={classes.chip}/>
		</Box>
		<Box className={classes.blogsection}>
		<Typography variant="subtitle1" style={{textAlign:'left'}} gutterBottom>There goes your blogs...</Typography>
		<List>
		{
			blogArray.blogDetails.map((item)=><ListItem className={classes.blogs} key={item.blogId}>
				<Card
				className={classes.card}
			  	id={item.blogId}>
			  	<CardHeader
			  	title={item.blogTitle}
			  	subheader={'by '+item.author}/>
			  	<CardActions style={{width:'100%'}}>
			  	  <Button className={classes.button} onClick={()=>handleBlogRead(item.blogId)}>Read</Button>
			  	  <Button className={classes.button} onClick={()=>handleBlogPublish(item.blogId)}>Publish</Button>
			  	</CardActions>
			  	</Card>
				</ListItem>)
		}
		</List>
		</Box>
        {
            blogPublishFlag ? <FirebaseBlogPublish blogId={blogId} setBlogPublishFlag={setBlogPublishFlag}/> : null
        }
		</div>)
	}
	else{
		return(<div>
            <Navbar/>
            <Typography variant="subtitle1" style={{marginTop:"2%"}} gutterBottom>You haven't written any blogs OR wait still loading...</Typography>
            <Box className={classes.addBlog}>
            <Chip label="Want to write more blogs." onClick={handleBlog} className={classes.chip}/>
            </Box>
            </div>)
	}
}

export default UserWrittenBlogs;